.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 0.25s linear;
          animation: App-logo-spin infinite 0.25s linear;
  height: 80px;
}

.App-logo-notspinning {
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-grow {
  from { -webkit-transform: scale(1); transform: scale(1); }
  to { -webkit-transform: scale(5); transform: scale(5); }
}

@keyframes App-logo-grow {
  from { -webkit-transform: scale(1); transform: scale(1); }
  to { -webkit-transform: scale(5); transform: scale(5); }
}

@-webkit-keyframes App-logo-spin {
  0% { -webkit-transform: rotate(0deg) scale(1); transform: rotate(0deg) scale(1); }
  25% { -webkit-transform: rotate(90deg) scale(2); transform: rotate(90deg) scale(2); }
  50% { -webkit-transform: rotate(180deg) scale(1); transform: rotate(180deg) scale(1); }
  75% { -webkit-transform: rotate(270deg) scale(2); transform: rotate(270deg) scale(2); }
  100% { -webkit-transform: rotate(360deg) scale(1); transform: rotate(360deg) scale(1); }
}

@keyframes App-logo-spin {
  0% { -webkit-transform: rotate(0deg) scale(1); transform: rotate(0deg) scale(1); }
  25% { -webkit-transform: rotate(90deg) scale(2); transform: rotate(90deg) scale(2); }
  50% { -webkit-transform: rotate(180deg) scale(1); transform: rotate(180deg) scale(1); }
  75% { -webkit-transform: rotate(270deg) scale(2); transform: rotate(270deg) scale(2); }
  100% { -webkit-transform: rotate(360deg) scale(1); transform: rotate(360deg) scale(1); }
}

#form-container {
  padding-top: 25px;
}

#button-container {
  padding-top: 25px;
}

#sbtn {
  margin-right: 5px;
}

#tbtn {
  margin-left: 5px;
}


